    // Forms
    //  ajax  XMLHttpRequest
    const forms = document.querySelectorAll('form');

    const message = {
        loading: 'img/form/spinner.svg',
        success: '!     ',
        failture: '-   ...'
    };

    forms.forEach(item => {
        // postDataFormData(item);
        bindPostData(item);
    });

    const postData = async (url, data) => {

        //    ( ,      )
        // (fetch    ,  res  ) 
        //    -   async
        // await -   ,     , -  
        const res = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-type': 'application/json'
            },
            body: data 
        });      
        
        //             
        //  return 
        return await res.json(); //    json
    };

    //     json
    //  Fetch
    function bindPostData(form){
        form.addEventListener('submit', (e) => {
            e.preventDefault();

            // spinner
            const statusMessage = document.createElement('img');
            statusMessage.src = message.loading;
            statusMessage.style.cssText = `
                display: block;
                margin: 0 auto;
            `;
            form.insertAdjacentElement('afterend', statusMessage);

            //        FormData
            // !   input-     name
            const formData = new FormData(form);

            //  FormData    ( )
            //  FormData     (): ['-', ].   entries()
            //      (Object)
            //      json Object.fromEntries()
            const json = JSON.stringify(Object.fromEntries(formData.entries()));
   
            //  FormData    (  )
            // const object = {};
            // formData.forEach(function(value, key){
            //     object[key] = value;
            // });
            //     json JSON.stringify(object)     - postData

            postData('http://localhost:3000/requests', json) 
            .then(data => { //  ok
                showThanksModal(message.success);
                statusMessage.remove();
            }).catch(() => { //  
                showThanksModal(message.failture);
            }).finally(() => {
                form.reset();
            });

        });
    }

     //     FormData
    //  Fetch
    function postDataFetchFormData(form){
        form.addEventListener('submit', (e) => {
            e.preventDefault();

            // spinner
            const statusMessage = document.createElement('img');
            statusMessage.src = message.loading;
            statusMessage.style.cssText = `
                display: block;
                margin: 0 auto;
            `;
            form.insertAdjacentElement('afterend', statusMessage);

            //        FormData
            // !   input-     name
            const formData = new FormData(form);

            fetch('server.php', {
                method: 'POST',
                // headers: {
                //     'Content-type': 'application/json'
                // },
                body: formData
            }).then((data) => data.text()) //      
            .then(data => { //  ok
                showThanksModal(message.success);
                statusMessage.remove();
            }).catch(() => { //  
                showThanksModal(message.failture);
            }).finally(() => {
                form.reset();
            });

        });
    }

    //     json
    // .  XMLHttpRequest
    function postDataXMLHttpRequestJson(form){
        form.addEventListener('submit', (e) => {
            e.preventDefault();

            const statusMessage = document.createElement('img');
            statusMessage.src = message.loading;
            statusMessage.style.cssText = `
                display: block;
                margin: 0 auto;
            `;
            form.insertAdjacentElement('afterend', statusMessage);

            const request = new XMLHttpRequest();
            request.open('POST', 'server.php');

            request.setRequestHeader('Content-type', 'application/json');

            //        FormData
            // !   input-     name
            const formData = new FormData(form);

            //  FormData    
            const object = {};
            formData.forEach(function(value, key){
                object[key] = value;
            });

            //    json
            const json = JSON.stringify(object);

            request.send(json);

            request.addEventListener('load', () => {
                if (request.status === 200){
                    showThanksModal(message.success);
                    form.reset();
                    statusMessage.remove();
                } else {
                    showThanksModal(message.failture);
                }
            });

        });
    }

    //     FormData
      // .  XMLHttpRequest
      function postDataXMLHttpRequestFormData(form){
        form.addEventListener('submit', (e) => {
            e.preventDefault();

            const statusMessage = document.createElement('div');
            statusMessage.classList.add('status');
            statusMessage.textContent = message.loading;
            form.append(statusMessage);

            const request = new XMLHttpRequest();
            request.open('POST', 'server.php');

            //   ,   Missing boundary in multipart/form-data POST data in 
            // :    ,   
            // https://inoyakaigor.ru/blog/94
            // request.setRequestHeader('Content-type', 'multipart/form-data');

            //        FormData
            // !   input-     name
            const formData = new FormData(form);
 
            //   openserver HTTP apache,   .
            //   Nginx 1.12  apache + nginx
            request.send(formData);

            request.addEventListener('load', () => {
                if (request.status === 200){ 
                    statusMessage.textContent = message.success;
                    form.reset();
                    setTimeout(() => {
                        statusMessage.remove();
                    }, 2000);
                } else {
                    statusMessage.textContent = message.failture;
                }
            });

        });

    }

    // -     
    function showThanksModal(message){
        const prevModalDialog = document.querySelector('.modal__dialog');

        prevModalDialog.classList.add('hide');
        openModal();

        const thanksModal = document.createElement('div');
        thanksModal.classList.add('modal__dialog');
        thanksModal.innerHTML = `
            <div class="modal__content">
                <div data-close class="modal__close">&times;</div>
                <div class="modal__title">${message}</div>
            </div>
        `;
        
        document.querySelector('.modal').append(thanksModal);

        //  4         
        setTimeout(() =>{
            thanksModal.remove();
            prevModalDialog.classList.add('show');
            prevModalDialog.classList.remove('hide');
            closeModal();
        }, 4000);
    }
